import { mapState, mapGetters, mapMutations } from 'vuex'
import { pirithList } from '@/pirith-list.js'
const rpcTextAr = t => t.split(' ').filter(x => x.length).map(x => parseInt(x))

export default {
  name: 'Settings',

  data: function() {
    return {
      rules: [
        v => v.search(/[^\d ]/) == -1 || 'à¶à¶à¶ à·à· à·à·à·à·à¶­à·à¶±à· (space) à¶´à¶¸à¶«à¶à· à¶ºà·à¶¯à¶±à·à¶±',
        v => rpcTextAr(v).length >= 2 || 'à¶à·à¶¸ à¶à¶à¶ à¶¯à·à¶à¶à· à·à¶­à· à¶­à·à¶¶à·à¶º à¶ºà·à¶­à·à¶º',
        v => rpcTextAr(v).length <= 5 || 'à¶à¶´à¶»à·à¶¸ à¶à¶à¶ 5à¶à· à¶à¶à· à¶à¶»à¶±à·à¶±',
        v => rpcTextAr(v)[0] == 1 || 'à¶´à·à¶¸à· à¶à¶à¶à¶º à¶à¶ à·à·à¶º à¶ºà·à¶­à·à¶º',
        v => Math.max(...rpcTextAr(v)) <= 100000 || 'à¶à¶´à¶»à·à¶¸ à¶à¶à¶à¶º à¶½à¶à·à·à¶ºà¶à· (100000) à·à·à¶º à¶ºà·à¶­à·à¶º',
      ],
      pronounChanges: [
        ['à¶­à·', 'à¶¸à·'],
        ['à¶­à·', 'à¶¸à¶¸'],
        ['à¶­à·à·à¶', 'à¶¸à¶'],
        ['à¶­à·à¶ºà·à·à¶', 'à¶¸à¶ºà·à·à¶'],
        ['à¶´à¶»à·à¶à·à¶à·à¶­à·à·à·', 'à¶´à¶»à·à¶à·à¶à·à¶­à·à·à·à¶¸à·'], //in jinapanjara
        ['à¶ à¶»à·à·à·', 'à¶ à¶»à·à¶¸à·'], //in jinapanjara
      ],
    }
  },
  
  computed: {
    ...mapState(['repeatCounts', 'playMetrics']),
    repeatCountsText: { 
      get() { return this.repeatCounts.join(' ') },
      set(text) {
        if (this.rules.some(rule => rule(text) !== true)) return // some rule failed
        this.$store.commit('setRepeatCounts', rpcTextAr(text)) 
      },
    },
    metrics() {
      return this.playMetrics.map((count, i) => ({ name: pirithList[i][2], count }))
        .filter(m => m.count)
        .sort((a, b) => b.count - a.count)
    },
  },
}
